import React, { Component } from 'react'
import { css } from '@emotion/core'
import { 
  Form, 
  Input,
  Button,
  Card,
  Select,
  Row,
  Modal,
  Col
} from 'antd'

const Option = Select.Option

const formLayout = {
  labelCol: { span: 5 },
  wrapperCol: { span: 18 }
}

const ButtonLayout = {
  wrapperCol: {
    span: 6,
    offset: 19
  }
}

class PointOfSales extends Component {
  state = {
    ModalText: 'Content of the modal',
    visible: false,
    confirmLoading: false,
  }

  showModal = () => {
    this.setState({
      visible: true,
    })
  }

  handleOk = () => {
    this.setState({
      ModalText: 'The modal will be closed after two seconds',
      confirmLoading: true,
    })
    setTimeout(() => {
      this.setState({
        visible: false,
        confirmLoading: false,
      })
    }, 2000)
  }

  handleCancel = () => {
    // console.log('Clicked cancel button')
    this.setState({
      visible: false,
    })
  }
  render(){
    const { visible, confirmLoading, ModalText } = this.state
    return (
      <Card css={css`
        padding-bottom: 0;
        border-radius: 8px;
        border-top: 4px solid rgb(24, 144, 255);
        box-shadow: 1px 4px 15px rgba(24, 144, 255, 0.1);
        @media(max-width: 700px) {
          width: 290px;
        }
      `}>
        <Form {...formLayout}>
          <Row gutter={16}>
            <Col lg={{span: 12}} xs={{ span: 24 }}>
              <Form.Item label="Transaction No">
                <Input
                  placeholder="No. Transaksi"
                />
              </Form.Item>
              <Form.Item label="Vehicle Type">
                <Select
                  showSearch
                  placeholder="Pilih Jenis Kendaraan"
                >
                  <Option value="MotorCycle">MotorCycle</Option>
                  <Option value="Car">Car</Option>
                </Select>
              </Form.Item>
              <Form.Item label="Vehicle No">
                <Input />
              </Form.Item>
              <Form.Item label="In">
                <Input disabled={true} placeholder="Waktu Masuk" />
              </Form.Item>
              <Form.Item label="Out">
                <Input disabled={true} placeholder="Waktu Keluar"/>
              </Form.Item>
              <Form.Item label="Duration">
                <Input disabled={true} placeholder="Lama Parkir"/>
              </Form.Item>
              <Form.Item label="Vehicle Type">
                <Select
                  showSearch
                  placeholder="Pilih Type Pengguna"
                >
                  <Option value="Member">Member</Option>
                  <Option value="Non Member">Non Member</Option>
                  <Option value="Corporate">Corporate</Option>
                </Select>
              </Form.Item>
            </Col>
  
            <Col lg={{span: 12}} xs={{ span: 24 }}>
              <Form.Item label="Pay / Forfeit">
                <Select
                  showSearch
                  defaultValue="Bayar"
                >
                  <Option value="Bayar">Bayar</Option>
                  <Option value="Denda">Denda</Option>
                </Select>
              </Form.Item>
              <Form.Item label="Price">
                <Input disabled={true} placeholder="Harga"/>
              </Form.Item>
              <Form.Item label="Discount Type">
                <Select
                  showSearch
                  placeholder="Pilih Tipe Diskon"
                >
                  <Option value="Hari Raya Idul Fitri">Hari Raya Idul Fitri</Option>
                  <Option value="Hari Raya Idul Adha">Hari Raya Idul Adha</Option>
                </Select>
              </Form.Item>
              <Form.Item label="Nominal">
                <div css={css`
                    font-size: 4em; 
                    font-weight: bold;
                    padding: 32px 15px;
                    box-sizing: border-box;
                    background: #f0f2f5; 
                `}>Rp. 4000</div>
              </Form.Item>
              <Form.Item label="Payment Type">
                <Select
                  showSearch
                  placeholder="Select a Payment Type"
                >
                  <Option value="Cash">Cash</Option>
                  <Option value="Non Cash">Non Cash</Option>
                </Select>
              </Form.Item>
              <Form.Item label="Bill Payment">
                <Input placeholder="Bayar"/>
              </Form.Item>
              <Form.Item {...ButtonLayout}>
                <Button type="primary" onClick={this.showModal}>Process</Button>
              </Form.Item>
              <Modal
                title="Title"
                visible={visible}
                onOk={this.handleOk}
                confirmLoading={confirmLoading}
                onCancel={this.handleCancel}
              >
                <p>{ModalText}</p>
              </Modal>
            </Col>
          </Row>
        </Form>
      </Card>
    )
  }
}

export default PointOfSales